@charset "utf-8";
/**
 * main page style
 */

.card {
    display: block;
    min-height: 1px;
    margin: 30px auto;
    padding: 40px;
    background-color: #ffffff;
    box-shadow: 0 4px 10px rgba(0,0,0,0.05), 0 0 1px rgba(0,0,0,0.1);
    border-radius: 4px;
}

.card-container{
    min-height: calc(100vh - 70px - 55px);  // minus navbar and footer
    max-width: $content-width;
    margin: 0 auto;
}


/* navigation bar related*/
.navbar {
    background-color: #ffffff;
    box-shadow: none;
    //box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    border-bottom: 1px solid #e8e8e8;
}

.navbar-logo {
    height: 50px;
}

.navbar-container {
    max-width: $content-width;
    margin: 0 auto;
    padding: 10px 0;
    display: flex; 
    align-items: center; 
    justify-content: space-between;
}

.main-menu {
    list-style-type: none;
    //background-color: antiquewhite;
    margin: 0;
    padding: 0;
    & > li {
        display: inline-block;
        padding: 10px;
    }
    & > li > a {
        font-size: 1.2rem;
        color: $grey-color-dark;//$iros-eggplant;
        &:hover {
            text-decoration: none;
            font-weight: bold;
        }
    }
}


/*aboutme page */
.about-img {
    float: left;
    height: 300px;
//margin-top: 20px;
//margin-left: auto;
    margin-right: 45px;
    margin-bottom: 30px;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
}

.about-cv a {
    background-color: $background-color;
    border: 1px solid $grey-color-light;
    border-radius: 4px;
    padding: 10px;
    color: $text-color;
    &:hover {
        background-color: darken($background-color, 10%);
        color: $hover-color;
    }
}

.about-icon {
    i.fa, i.ai {
        color: $iros-eggplant;
        transition: color 0.4s ease 0s;
        &:hover {
          color: $hover-color;
        }
    }
}


/* post related */
.post-meta {
    color: #7a7a7a;
    font-size: 0.75rem;
    a {
        color: inherit;
        &:hover {
            color: $hover-color;
        }
    }
}


/* useless ???
.post-content {
    font-size: $base-font-size;
}
*/

/*footer related*/
.footerbar {
    //min-height: 20px;
    display: flex;
    background-color: #ffffff;
}

.footerbar-container {
    max-width: $content-width;
    margin: 15px auto;
}

.footerbar-content {
    color: $grey-color-dark;
    font-size: $tiny-font-size;
    a {
        color: $text-color;
        text-decoration: none;
    }
}


/* tags */
#tag-a {
    margin: 10px 10px;
    font-size: $small-font-size;
    &:hover {
        text-decoration: none;
    }
}

.tag-left {
    background-color: #5c5c5c;
    padding: 5px 10px;
    margin: 0;    
    border-radius: 4px 0 0 4px;
    color: #fff;
}

.tag-right {
    background-color: darken($background-color, 10%);
    padding: 5px 10px;
    margin-left: -5px;
    border-radius: 0 4px 4px 0;
    color: $text-color;
}


/* archives */
.archive-box{
    li {
      list-style: none;
      font-weight: bold;
      margin-top:10px;
    }
}

/* categories */
.category-box{
    padding: 10px;
    margin: 0 20px;
    display: flex; 
    justify-content: space-between;
    color: $text-color;

    &:hover {
        background-color: $background-color;
        text-decoration: none;
        color: inherit;
    }
}


/* publications */
.pub-container {
    padding: 0;
    margin-bottom: 2rem;
    //background-color: #c4661f;
}

.pub-meta {
    line-height: 1em;
    color: $grey-color;
    h4 {
        font-weight: bold;
        color: $text-color;
    }
    b {
        color: $text-color;
        font-weight: normal;
    }
}

.pub-icon {
    padding: 5px 15px;
    margin: 10px;
    border-radius: 4px;
    border: 1px solid $text-color;
    font-size: $tiny-font-size;
    color: $text-color;
    &:hover {
        border: 1px solid $hover-color;
    }
}

.pub-content {    
    //border: none;
    //max-height: 0;
    //overflow: hidden;
    //border: none;
    //padding: 0;
    //margin: 0;

    max-width: 500px;  
    font-size: $small-font-size;
    color: $text-color;
    line-height: 1.4em;
    //transition: max-height 0.3s ease;
}

.hidden {
    max-height: 0px;
    border: none;
    padding: 0;
    margin: 0;
    overflow: hidden;
    margin-bottom: 10px;
    //transition: max-height 1s ease;
}

.open {
    margin: 0 5px;
    padding: 5px;
    max-height: 100rem;
    border: 1px dashed $text-color;
    transition: max-height 1s ease;
}


/* pagination */
.pagination {
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.pagination-pre-next {
    padding: 4px 8px;
    border-radius: 4px;
    background-color: #fff;
    color: $text-color;
    font-size: $small-font-size;
    /*&:hover {
        text-decoration: none;
        color: inherit;
    }*/
}

.pagination-icon {
    padding: 3px 10px;
    margin: 0 5px;
    color: $text-color;
    background-color: #fff;
    border-radius: 4px;
    &:hover {
        color: inherit;
        text-decoration: none;
    }
}

.no-hover-effect {
    &:hover {
        color: $text-color;
        text-decoration: none;
    }
}

.pagination-icon-selected {
    background-color: $brand-color;
    color: #fff;
}


#back-top-button {
    display: none;
    color: #fff;
    padding: 0 10px;
    padding-top: 8px;
    border-radius: 10px;
    background-color: $grey-color-light;
    position: fixed;
    bottom: 100px;
    right: 450px;
    &:hover {
        background-color: $grey-color-dark;
    }
}